<template>
  <div class="container">
    <div v-if="AndroidStatus" class="android-mask" @touchstart="handleTouchStart">
      <div class="tips">安卓用户请点击屏幕播放视频~</div>
    </div>
    <div class="mask" @touchstart="handleClick"></div>
    <video
      width="100%"
      height="667px"
      id="video"
      muted
      autoplay
      loop
      :poster="isAndroid ? 'https://wangpengxiang-1259750994.cos.ap-beijing.myqcloud.com/Docu/image/2022/04/21/background_phone.jpeg' : ''"
      x5-playsinline
      playsinline
      x-webkit-airplay="true"
      webkit-playsinline="true"
      x5-video-player-type="true"
      x5-video-orientation="h5"
      x5-video-player-fullscreen="true"
    >
      <source src="https://wangpengxiang-1259750994.cos.ap-beijing.myqcloud.com/Docu/video/2022/04/21/1650524998193215.mp4" type="video/mp4">
      <source src="https://wangpengxiang-1259750994.cos.ap-beijing.myqcloud.com/Docu/video/2022/04/21/1vnvl-z2kpr.webm" type="video/webm">
      <source src="https://wangpengxiang-1259750994.cos.ap-beijing.myqcloud.com/Docu/video/2022/04/21/5ga35-ram2u.mov" type="video/mov">
      <source src="https://wangpengxiang-1259750994.cos.ap-beijing.myqcloud.com/Docu/video/2022/04/21/rsfkw-8cbiz.flv" type="video/flv">
      您的浏览器不支持 HTML5 video 标签。
    </video>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      isAndroid: false
    }
  },
  computed: {
    AndroidStatus() {
      if (this.isAndroid && /MicroMessenger/i.test(navigator.userAgent)) {
        return true
      } else {
        return false
      }
    }
  },
  mounted() {
    const video = document.getElementById("video")
    if ((navigator.userAgent.indexOf('Android') > -1 || navigator.userAgent.indexOf('Adr') > -1) && /MicroMessenger/i.test(navigator.userAgent)) {
      this.isAndroid = true
      video.click()
      video.play()
      video.muted = true
    }
    if (typeof WeixinJSBridge !== "undefined") {
      // eslint-disable-next-line
      WeixinJSBridge.invoke('getNetworkType',{}, () => {
        // 利用该方法进行自动播放
        video.play()
        video.muted = true
      })
    } else {
      video.play()
      video.muted = true
    }
  },
  methods: {
    handleTouchStart() {
      this.isAndroid = false
    },
    handleClick() {
      this.$router.push('/phone/index')
    }
  }
}
</script>

