        font-weight: bold;
        font-family: 'Hanzipen SC', 'Yapi SC', 'HYHeiLiZhiTiJ', 'HYKaiti', 'STXingkai', 'Microsoft YaHei';
      }
    }
    .mask {
      width: 100%;
      height: 100%;
