      @include fixPos(0, auto, auto, auto, 5);
      background: transparent;
    }
    video {
      width: inherit;
      height: inherit;
      // mix-blend-mode: screen;
      object-fit: cover;
      @include fixPos(0, auto, auto, 0, -1);
    }
  }
  .container:before {
    content: '';
    display: block;
    padding-top: calc(1 / var(--container) * 100%);
  }
